div.software.game.grid
{
    margin: 0 auto;
    width: fit-content;

    div.container
    {
        display: inline-flex;
        gap: 20pt;
        margin: 0pt 40pt !important;
        width: fit-content;
    }

    a.grid-item
    {
        min-width: 100pt;
        max-width: 200pt;
        img
        {
            width: 100%;
            aspect-ratio: 1 / 1;
        }
    }
}

div.software.blendaddon.grid
{
    margin: 0 auto;
    width: fit-content;

    div.container
    {
        display: inline-flex;
        gap: 20pt;
        margin: 0pt 40pt !important;
        width: fit-content;
    }

    a.grid-item
    {
        display: grid;
        overflow: hidden;
        text-decoration: none;
        color: inherit;

        min-width: 160pt;
        max-width: 300pt;

        img
        {
            grid-row: 1;
            grid-column: 1;
            width: 100%;
            aspect-ratio: 3 / 1;
        }
        p
        {
            font-family: 'FrancoBold';
            font-size: 30pt;
            grid-row: 1;
            grid-column: 1;
            margin: auto 20pt auto 20pt
        }
    }
}
