.nav-desktop
{
    @media only screen and (max-width: 1000px) 
    {
        display: none;
        visibility: collapse;
    }
}
.nav-mobile
{
    visibility: collapse;
    @media only screen and (max-width: 1000px) 
    {
        visibility: visible;
    }
    

}
.nav-mobile-padding
{
    height: 0;
    visibility: collapse;
    @media only screen and (max-width: 1000px) 
    {
        height: 58pt;
        visibility: visible;
    }
}
.nav-overlay
{
    z-index: 998;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    // gap: 20pt;

    background-color: $col-background;

    a
    {
        padding: 0 10pt 0 10pt;
        font-family: 'FrancoRegular';
        font-size: 16pt;

        text-decoration: none;
        text-align: right;
        color: inherit;

        border-width: 4pt;
        border-style: none none none solid;
        border-color: $col-accent;
        transition: all 0.5s;

        margin-left: 30pt;
        margin-right: auto;
    }
    a:hover
    {
        transition: all 0.5s;
        border-color: $col-foreground;
    }

    transition: all 0.5s;

    @media only screen and (min-width: 1000px) 
    {
        left: -100vw;
        transition: all 0.5s;
    }
}
.nav-overlay.hidden
{
    left: -100vw;
}
.nav-floatable
{
    width:100vw;
    transition: all 0.5s;
    background: #00000000;
    
    @media only screen and (max-width: 1000px) 
    {
        max-width:100vw;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;
    }
}
html:not([data-scroll='0']) .nav-floatable
{
    @media only screen and (max-width: 1000px){
        transition: all 0.5s;
        background: #000000aa;
    }
}
a.nav{
    font-family: 'FrancoRegular';
    font-size: 14pt;
    text-decoration: none;
    text-align: center;
    color: inherit;
    div
    {
        width: fit-content;
        padding: 20pt 15pt;
        i
        {
            font-size: 20pt;
        }
    }
}
.nav-spacer{
    flex-grow:1;
}
.nav-pad{
    margin-left:20px;
}
.nav-pad-v{
    margin-bottom:20px;
}