body {
    font-family: 'FrankoLight';
    font-weight: normal;
    font-style: normal;
    font-size: 13pt;

    margin: 0px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;

    @media only screen and (max-width: 400px) 
    {
        font-size: 11pt;
    }
}

h1, h2, h3, h4, h5, h6
{
    font-family: 'FrancoRegular';
}
h1.p-light, h2.p-light, h3.p-light, h4.p-light, h5.p-light, h6.p-light
{
    font-family: 'FrancoLight';
}
h1.p-bold, h2.p-bold, h3.p-bold, h4.p-bold, h5.p-bold, h6.p-bold
{
    font-family: 'FrancoBold';
}

h1{
    font-size: 60pt;
}
h2{
    font-size: 40pt;
}
h3{
    font-size: 30pt;
}
h4{
    font-size: 20pt;
}
h5{
    font-size: 13pt;
}
h6{
    font-size: 9pt;
}

@media only screen and (max-width: 400px) 
{
    h1{
        font-size: 40pt;
    }
    h2{
        font-size: 30pt;
    }
    h3{
        font-size: 25pt;
    }
    h4{
        font-size: 18pt;
    }
    h5{
        font-size: 11pt;
    }
    h6{
        font-size: 8pt;
    }
}

img{
    object-fit: cover;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}


@font-face {
	font-family:"icons";
	src:url("https://cdn.kde.org/breeze-icons/icons.ttf");
	font-weight:normal;
	font-style:normal;
}
.icon {
    font-family:"icons";
    display:inline-block;
    line-height:1;
    font-weight:normal;
    font-style:normal;
    speak:none;
    text-decoration:inherit;
    text-transform:none;
    text-rendering:auto;
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale;
}
.icon_edit-find:before {
	content:"edit-find";
}
.icon_application-menu:before {
	content:"application-menu";
}

a
{
    transition: all 0.5s;
    text-decoration: none;
    color: $col-accent;
}
a:hover
{
    transition: all 0.5s;
    color: $col-foreground;
}

b{
    font-family: 'FrankoBold';
}