div.blog.banner
{
    display: grid;
    width: 100%;
    height: 200pt;
    overflow: hidden;
    img
    {
        grid-row: 1;
        grid-column: 1;
        width: 100%;
        height: 200pt;
    }
    div
    {
        grid-row: 1;
        grid-column: 1;
        margin: auto;
        h3
        {
            margin: auto 35pt;
        }
    }
}
div.blog.banner.compact
{
    height: 90pt;
    img
    {
        grid-row: 1;
        grid-column: 1;
        width: 100%;
        height: 90pt;
    }
}
div.blog.info
{
    display: flex;
    flex-direction: row;
    
    // @media only screen and (max-width: 600px) 
    // {
    //     flex-direction: column;
    // }
    
    .spacer {
        @media only screen and (min-width: 600px) 
        {
            flex-grow: 1;
        }
    }
}
div.blog.content
{
    img
    {
        margin: 20pt auto 0 auto;
        object-fit: contain;
        width: 100%;
        max-height: 90vh;
    }
}