div.linktree
{
    width: 100%;
    margin: 40pt 0 100pt 0;
    align-items: center;
    text-align: center;
    gap: 20pt;

    a
    {
        width: calc(100% - 100pt);
    }
    div.social-container
    {
        gap: 10pt;
        margin: -20pt 50pt 0 50pt;

        display: inline-flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-content: center;
        justify-content: center;

        a
        {
            width: initial;
        }

        img.social-icon
        {
            width: 30pt;
            height: 30pt;
            filter: invert(100%);
        }
    }
    h5
    {
        margin-top: 50pt;
    }
}