.align-right
{
    margin-left: auto !important;
}

.fill_width
{
    width:100%;
}

.flex-vertical
{
    display: flex;
    flex-direction: column;
}
.flex-horizontal
{
    display: flex;
    flex-direction: row;
}

.always-on-top
{
    position: relative;
    z-index: 990;
}

.post-contain
{
    margin: 20pt auto;
    div
    {
        margin: 0 35pt;
    }
    div.no-remargin
    {
        margin: initial;
    }
    @media only screen and (min-width: 1300px) 
    {
        width: 900pt;
    }
    @media only screen and (max-width: 1300px) 
    {
        width: 100vw;
    }
}

.trim
{
    margin: auto;
    padding: 0;
}