div.bloglist.leading
{
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: hidden;

    a
    {
        min-width: 400pt;
        flex-grow: 1;
        height: 350pt;

        text-decoration: none;
        text-align: right;
        color: inherit;

        display: grid;

        @media only screen and (max-width: 640px)
        {
            min-width: initial;
        }
        
        img
        {
            grid-row: 1;
            grid-column: 1;
            width: 100%;
            height: 350pt;

            transition: all 1s;
            scale: 100%;
        }

        div
        {
            grid-row: 1;
            grid-column: 1;
            margin-left: auto;
            margin-right: 20pt;
            margin-top: 20pt;
            padding-left: 20pt;
            max-width: 400pt;
            scale: 100%;

            p.head
            {
                font-family: 'FrancoBold';
                font-size: 35pt !important;
                text-wrap: wrap;

                @media only screen and (max-width: 640px)
                {
                    font-size: 20pt !important;
                }
            }
            p
            {
                font-size: 15pt !important;
                text-wrap: wrap;

                @media only screen and (max-width: 640px)
                {
                    font-size: 10pt !important;
                }
            }
        }
    }
    a:hover
    {
        img
        {
            transition: all 1s;
            scale: 110%;
        }
    }
}
div.bloglist.grid
{
    display: grid;
    p.title
    {
        grid-row: 1;
        grid-column: 1;

        font-family: 'FrancoRegular';
        font-size: 20pt;
        margin: 0;
        padding: 9pt 8pt 7pt 8pt;
        background-color: black;

        width: fit-content;
        height: fit-content;
    }
    div.grid-container
    {
        grid-row: 1;
        grid-column: 1;

        display: grid !important;
        grid-template-columns: repeat(auto-fill, minmax(350pt, 1fr)) !important;

        @media only screen and (max-width: 640px)
        {
            grid-template-columns: repeat(auto-fill, minmax(200pt, 1fr)) !important;
        }
    }
    a.grid-item
    {
        display: grid;
        overflow: hidden;
        text-decoration: none;
        text-align: right;
        color: inherit;
        div.img-contain
        {
            width: 100%;
            aspect-ratio: 18 / 10;
            overflow: hidden;
        }
        img
        {
            aspect-ratio: 18 / 10;

            grid-row: 1;
            grid-column: 1;
            width: 100%;

            transition: all 1s;
            scale: 100%;
        }
        div.content
        {
            margin-top: 0pt;
            margin-left: 10pt;
            margin-right: 10pt;
            margin-bottom: auto;

            grid-row: 2;
            grid-column: 1;

            padding-top: 10pt;
            padding-bottom: 20pt;

            p.date
            {
                font-family: 'FrancoBold';
                margin: auto 0;
            }
            p.head
            {
                font-family: 'FrancoRegular';
                font-size: 26pt;
                margin: 0;

                @media only screen and (max-width: 640px)
                {
                    font-size: 20pt !important;
                }
            }
            p.tag
            {
                margin: 0;
            }
            p
            {
                font-size: 12pt;
                margin: 15pt 0 0 0;

                @media only screen and (max-width: 640px)
                {
                    font-size: 10pt !important;
                }
            }
            div.sub-info
            {
                margin: 0 0 15pt 0;
            }
        }
    }
    a.grid-item:hover
    {
        img
        {
            transition: all 1s;
            scale: 110%;
        }
    }
}