@keyframes fadein
{
    from
    {
        filter: brightness(0%);
        scale: 110%;
    }
    to
    {
        filter: brightness(100%);
        scale: 100%;
    }
}

div.home.head
{
    margin-top: -58pt;
    width: 100%;
    height: 100vh;
    display: grid;
    overflow: hidden;
    a
    {
        grid-column: 1;
        grid-row: 1;
        filter: brightness(0%);
        img
        {
            width: 100%;
            height: 100vh;
            
            @media only screen and (max-aspect-ratio: 1 / 1)
            {
                width: 100%;
                height: initial;
                aspect-ratio: 1 / 1;
            }
        }
    }
    a.ready
    {
        animation-name: fadein;
        animation-duration: 3s;
        animation-iteration-count: 1;
        filter: brightness(100%);
    }
    a.hidden
    {
        opacity: 0;
        visibility: hidden;
        transition: all 0.5s;
    }
    div.newsbar
    {
        grid-row: 1;
        grid-column: 1;
        margin-top: auto;
        width: 100vw;
        background-color: $col-foreground;
        color: $col-background;
        z-index: 800;
        display: flex;
        flex-direction: row;

        p
        {
            font-family: 'FrancoRegular';
            font-size: 12pt;
            margin: 11pt 14pt 9pt 14pt;

            flex-grow: 1;
            flex-wrap: wrap;

            @media only screen and (max-width: 640px)
            {
                font-size: 10pt;
                margin: 9pt 12pt 7pt 12pt;
            }
        }

        p.label
        {
            width: fit-content;
            margin: 0;
            padding: 11pt 14pt 9pt 14pt;
            background-color: $col-background;
            color: $col-foreground;

            flex-grow: 0;
            flex-shrink: 0;

            @media only screen and (max-width: 640px)
            {
                padding: 9pt 12pt 7pt 12pt;
            }
        }
        a
        {
            color: $col-background;
        }
        a:hover
        {
            color: $col-background;
        }
    }

    @media only screen and (max-aspect-ratio: 1 / 1)
    {
        width: 100%;
        height: initial;
        aspect-ratio: 1 / 1;
    }
}
div.home.grid
{
    display: grid;
    p.title
    {
        grid-row: 1;
        grid-column: 1;

        font-family: 'FrancoRegular';
        font-size: 20pt;
        margin: 0;
        padding: 9pt 8pt 7pt 8pt;
        background-color: black;

        width: fit-content;
        height: fit-content;

        @media only screen and (max-width: 500px)
        {
            font-size: 16pt;
        }
    }
    div.grid-container
    {
        grid-row: 1;
        grid-column: 1;

        display: grid !important;
        grid-template-columns: repeat(auto-fill, minmax(240pt, 1fr)) !important;

        @media only screen and (max-width: 640px)
        {
            grid-template-columns: repeat(auto-fill, minmax(120pt, 1fr)) !important;
        }
    }
    div.grid-container.blog
    {
        grid-template-columns: repeat(auto-fill, minmax(400pt, 1fr)) !important;

        @media only screen and (max-width: 640px)
        {
            grid-template-columns: repeat(auto-fill, minmax(200pt, 1fr)) !important;
        }
    }
    a.grid-item
    {
        display: grid;
        overflow: hidden;

        text-decoration: none;
        text-align: right;
        color: inherit;
        
        img
        {
            aspect-ratio: 1 / 1;

            grid-row: 1;
            grid-column: 1;
            width: 100%;

            transition: all 1s;
            scale: 100%;
        }
        img.blog
        {
            aspect-ratio: 18 / 10;
            @media only screen and (max-width: 640px)
            {
                aspect-ratio: 3 / 1;
            }
        }
        div
        {
            scale: 100%;

            margin: auto 10pt 10pt 10pt;
            grid-row: 1;
            grid-column: 1;
            @media only screen and (max-width: 640px)
            {
                margin: auto 5pt 5pt 5pt;
            }
            p.head
            {
                font-family: 'FrancoRegular';
                font-size: 26pt;
                margin: 0;
                @media only screen and (max-width: 640px)
                {
                    font-size: 18pt;
                    visibility: visible;
                }
            }
            p
            {
                font-size: 12pt;
                margin: 15pt 0 0 0;
                @media only screen and (max-width: 640px)
                {
                    margin: 0;
                    font-size: 0;
                    visibility: collapse;
                }
            }
        }
    }
    a.grid-item:hover
    {
        img
        {
            transition: all 1s;
            scale: 110%;
        }
    }
}