div.footer
{
    margin: auto 0 0 0;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 100px;
    background-color: rgba(255, 255, 255, 0.05);

    div.container
    {
        margin: 0 auto;
        max-width: 1000pt;
        // height: 100%;
        div.pane
        {
            margin-top: auto;
            margin-bottom: auto;
        }
        div.pane:first-child
        {
            margin-left: 30pt;
        }
        div.pane:last-child
        {
            margin-right: 30pt;
        }
        div.nav
        {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            max-width: 500pt;
            gap: 20pt;

            @media only screen and (max-width: 800px) 
            {
                visibility: collapse;
            }

            a
            {
                padding: 0 10pt 0 10pt;
                font-family: 'FrancoRegular';
                font-size: 16pt;

                text-decoration: none;
                text-align: right;
                color: inherit;

                border-width: 4pt;
                border-style: none none none solid;
                border-color: $col-accent;
                transition: all 0.5s;
            }
            a:hover
            {
                transition: all 0.5s;
                border-color: $col-foreground;
            }
        }
        div.connect
        {
            min-width: 300pt;
            max-width: 300pt;

            @media only screen and (max-width: 800px) 
            {
                min-width: initial;
                max-width: initial;
            }

            div.flex-horizontal
            {
                flex-direction: row-reverse;
                flex-wrap: wrap;
                gap: 15pt;
            }
        }
    }
    img.social-icon
    {
        margin: 0;
        width: 26pt;
        height: 26pt;
        filter: invert(100%);
    }
}