div.skilltree
{
    display: flex;
    flex-direction: row;

    @media only screen and (max-width: 1000px)
    {
        flex-direction: column;
    }

    div.section-title
    {
        flex-grow: 0;
        flex-shrink: 0;
        width: 200pt;

        @media only screen and (max-width: 1000px)
        {
            margin: 0 auto;
        }

        img.skw
        {
            object-fit: contain;
            width: 150pt;
            height: 150pt;

            @media only screen and (max-width: 1000px)
            {
                flex-direction: column;
                width: 100pt;
                height: 50pt;
            }
        }
    }
    div.section-list
    {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 5pt;

        a
        {
            font-family: 'FrancoRegular';
            font-size: 20px;
            height: fit-content;

            text-decoration: none;
            text-align: right;
            color: inherit;
            
            margin: 0;
            padding: 17px 15px 13px 15px;

            background-color: rgba(255, 255, 255, 0.162);

            @media only screen and (max-width: 1000px)
            {
                font-size: 15px;
                padding: 13px 12px 11px 12px;
            }
        }
    }
}