@keyframes heartbeat
{
    from
    {
        opacity: 50%;
    }

    25%
    {
        opacity: 30%;
    }

    50%
    {
        opacity: 30%;
    }

    60%
    {
        opacity: 0%;
    }

    70%
    {
        opacity: 80%;
    }

    to
    {
        opacity: 50%;
    }
}

div.background
{
    position: fixed;
    z-index: -999;
    display: grid;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;

    img
    {
        grid-row: 1;
        grid-column: 1;
        width: 100%;
    }
    img.align-right
    {
        margin-left: auto;
        margin-right: 0;
    }
    img.align-bottom
    {
        margin-top: auto;
    }
    img.bottom-0
    {
        animation-name: heartbeat;
        animation-duration: 8s;
        animation-iteration-count: infinite;
        width: auto;
        height: 100vh;
    }
}