div.artwork
{
    display: flex;
    width: 100%;
    height: 100vh;
    margin-top: -60pt;

    @media only screen and (max-width: 800px)
    {
        height: fit-content;
        flex-direction: column;
        margin: 0 !important;
    }

    div.viewer
    {
        flex-grow: 1;
        background-color: rgb(38, 38, 38);

        overflow: scroll;

        @media only screen and (max-width: 800px)
        {
            height: fit-content;
            overflow: initial;
            gap: 5pt;
        }
        
        div.item
        {
            display: flex;
            width: 100%;
            min-height: 100vh;
            max-height: 100vh;
            height: 100vh;
            align-items: center;

            @media only screen and (max-width: 800px)
            {
                min-height: initial;
                max-height: initial;
                height: initial;
            }

            img
            {
                width: 100%;
                height: 100vh;
                object-fit: contain;

                @media only screen and (max-width: 800px)
                {
                    height: initial;
                }
            }

            #odysee-iframe
            {
                width: 100%; 
                height: 100%; 
                border-width:0;

                @media only screen and (max-width: 800px)
                {
                    aspect-ratio: 1 / 1 !important;
                }
            }
        }
    }
    div.info
    {
        margin-top: 60pt;
        
        min-width: 230pt;
        max-width: 230pt;
        flex-shrink: 0;
        flex-grow: 0;

        padding-left: 20pt;
        padding-right: 20pt;
        padding-bottom: 80pt;

        overflow: scroll;

        @media only screen and (max-width: 800px)
        {
            margin-top: 0;

            min-width: initial;
            max-width: initial;

            overflow: initial;
        }


    }
}